<script>
export default {
  name: "header1-component",
  props: {
    headerText: {
      type: String,
      default: "工程申請單",
    },
    showPrint: {
      type: Boolean,
      default: false,
    },
    printData: {
      type: Object,
    },
    printBy: {
      type: String,
    },
  },
  data() {
    return {
      nowDate: this.formatDate(new Date()),
    };
  },
  methods: {
    formatDate(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
  },
  mounted() {},
};
</script>

<template src="./header1.html"></template>
<style lang="sass" scoped src="./header1.sass"></style>
